@b_w_min: 4px;
@b_w_max: 6px;
@b_w_time: 3s;

@import "loading5.less";

.loading1{	
	display: inline-block;
	width: 40px;
	height: 40px;
	position: relative;
	animation: a_rotate  3s infinite steps(60);
	-webkit-animation: a_rotate  3s infinite steps(60); /* Chrome, Safari, Opera */
}
.loading1:before{
	content:"";
	position: absolute;
	top:0;
	left: 0;
	border-width:5px;
	border-right-width:0;
	border-color: rgba(0,0,0,0.5);
	border-style: solid;
	display: inline-block;
	width: 9px;
	height: 20px;
	
	margin: 5px;
	margin-right: 0;
	border-radius: 20px 0 0 20px;
	outline: solid 5px rgba(0,0,0,0);	
	
	animation: a_border_left  2s infinite;
	-webkit-animation: a_border_left  2s infinite; /* Chrome, Safari, Opera */
}

.loading1:after{
	content:"";
	position: absolute;
	top:0;
	right: 0;
	border-width:5px;
	border-left-width:0;
	border-color: rgba(0,0,0,0.5);
	border-style: solid;
	display: inline-block;
	width: 9px;
	height: 20px;
	margin: 5px;
	margin-left: 0;
	border-radius:0 20px 20px 0;
	outline: solid 5px rgba(0,0,0,0);
	
	animation: a_border_right  12s infinite;
	-webkit-animation: a_border_right  2s infinite; /* Chrome, Safari, Opera */
}


@keyframes a_rotate  {
 from {
  transform: rotate(0deg);  
  -moz-transform: rotate(0deg); /* Для Firefox */
  -ms-transform: rotate(0deg); /* Для IE */
  -webkit-transform: rotate(0deg); /* Для Safari, Chrome, iOS */
  -o-transform: rotate(0deg); /* Для Opera */
  }
 to {
  transform: rotate(360deg);
  -moz-transform: rotate(360deg); /* Для Firefox */
  -ms-transform: rotate(360deg); /* Для IE */
  -webkit-transform: rotate(360deg); /* Для Safari, Chrome, iOS */
  -o-transform: rotate(360deg); /* Для Opera */
  }
}
/* Chrome, Safari, Opera */
@-webkit-keyframes a_rotate  {
 from {
  transform: rotate(0deg);  
  -moz-transform: rotate(0deg); /* Для Firefox */
  -ms-transform: rotate(0deg); /* Для IE */
  -webkit-transform: rotate(0deg); /* Для Safari, Chrome, iOS */
  -o-transform: rotate(0deg); /* Для Opera */
  }
    to {
  transform: rotate(360deg);
  -moz-transform: rotate(360deg); /* Для Firefox */
  -ms-transform: rotate(360deg); /* Для IE */
  -webkit-transform: rotate(360deg); /* Для Safari, Chrome, iOS */
  -o-transform: rotate(360deg); /* Для Opera */
  }
}

